.ally,
.glyph {
  position: absolute;
}
.ally {
  top: 45px;
  left: 0;
  letter-spacing: -0.24px;
  line-height: 20px;
}
.glyph {
  top: calc(50% - 23.5px);
  left: calc(50% + 24.5px);
  background-color: #ef5858;
  width: 41px;
  height: 44px;
}
.vector-icon {
  position: absolute;
  height: 75%;
  width: 75%;
  top: 12.5%;
  right: 12.5%;
  bottom: 12.5%;
  left: 12.5%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.sf-symbol {
  position: absolute;
  height: 50%;
  width: 77.78%;
  top: 63.33%;
  left: 21.67%;
  letter-spacing: -0.15px;
  display: inline-block;
}
.bxbxs-category-alt {
  position: absolute;
  top: 0;
  left: 77px;
  width: 45px;
  height: 45px;
  overflow: hidden;
  text-align: center;
  font-size: var(--font-size-6xl);
  color: var(--basic-inactive-tab);
  font-family: var(--font-sf-pro-text);
}
.ally-parent {
  position: absolute;
  top: 335px;
  left: 122px;
  width: 131px;
  height: 65px;
}
.splash-screen {
  position: relative;
  background-color: var(--color-lightskyblue);
  width: 100%;
  height: 812px;
  overflow: hidden;
  text-align: left;
  font-size: 48px;
  color: var(--white);
  font-family: var(--font-sen);
}
